import React from "react";

type Props = {
  item: {
    qty: number;
  };
  increase: () => void;
  decrease: () => void;
};

export default function AddToCartButton({ item, increase, decrease }: Props) {
  if (!item) {
    return (
      <button className="rounded-full bg-blue-600 px-4 py-2 text-white font-semibold">
        Add +
      </button>
    );
  }

  return (
    <div className="flex items-center gap-4">
      <button
        onClick={decrease}
        className="h-10 w-10 rounded-xl border grid place-items-center bg-white"
      >
        âˆ’
      </button>
      <div className="min-w-[70px] text-center font-extrabold text-black text-2xl">
        {item.qty}
      </div>
      <button
        onClick={increase}
        className="h-10 w-10 rounded-xl border grid place-items-center bg-white"
      >
        +
      </button>
    </div>
  );
}